













































































































































































































































































	import uniIcon from "@/components/uni-icon/uni-icon.vue"
	import uniPopup from "@/components/uni-popup/uni-popup.vue"
	export default {
		data() {
			return {
				isShow: false,
				isBackgroundShow: false,
				isAli:false,
				isWeCaht: false,
				isNew: false,
				isPay: false,
				isclick1: true,
				isclick2: false,
				isclick3: false,
				isyufu: false
			}
			
		},
		components: {
			uniIcon,
		},
		methods: {
			// 购物车选中
			clickMethods1() {
				this.isclick1 = this.isclick1==true?this.isclick1=false:this.isclick1=true
			},
			clickMethods2() {
				this.isclick2 = this.isclick2==true?this.isclick2=false:this.isclick2=true
			},
			clickMethods3() {
				this.isclick3 = this.isclick3==true?this.isclick3=false:this.isclick3=true
			},
			// 进入提交页面
			enterSbumitPage() {
				this.isShow = !this.isShow;
				this.isBackgroundShow = !this.isBackgroundShow;
			},
			yufuMethod() {
				this.isyufu =true;
				this.isAli = false;
				this.isWeCaht = false;
			},
			AlipayMethod() {
				this.isAli = true;
				this.isWeCaht = false;
				this.isyufu = false;
			},
			WePayMethod() {
				this.isWeCaht = true;
				this.isAli = false;
				this.isyufu = false;
			},
			colseBtn() {
				this.isBackgroundShow = !this.isBackgroundShow;
				this.isAli = !this.isAli
				this.isWeCaht = !this.isWeCaht
				this.isyufu = !this.isyufu;
				this.isNew = !this.isNew;
				this.isPay = !this.isPay;
			},
			newAddress() {
				this.isShow = !this.isShow;
				this.isNew = !this.isNew;
				
			},
			payMethods() {
				this.isNew = !this.isNew;
				this.isPay = !this.isPay;
				this.isyufu = !this.isyufu;
			}
		}
}
